/* --------------------------------------------------------------------------------------- */
/* EMPTY LIST */
/* --------------------------------------------------------------------------------------- */

.emptyProductListBodyMobile {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;

    height: 100%;
    justify-items: center;
}

.mobileEmptyListHeaderText {
    grid-column: 1/-1;
    grid-row: 1;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    flex-wrap: wrap;
    text-align: center;
}

.mobileEmptyListCharacterImage {
    grid-column: 1/-1;
    grid-row: 2;
    height: 30rem;
    width: 30rem;
    object-fit: contain;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobileEmptyListCharacterImage img {
    height: auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonsWrapper {
    grid-row: 3;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: .5rem;
}


/* --------------------------------------------------------------------------------------- */
/* POPULATED LIST */
/* --------------------------------------------------------------------------------------- */


.activeListHeaderContainer {

    /* display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(4, auto); */
    height: auto;
    display: flex;
    flex-direction: column;
    /* height: 10rem; */
    justify-content: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    gap: .5rem;
}

.headerItem1 {
    grid-row: 1;
    grid-column: 1/-1;
    display: flex;
    flex-direction: column;
    gap: .5rem;

}

.pageSubtitleWrapper {
    color: var(--color-gray-01);
}

.pageTertiaryTitleWrapper {
    color: var(--color-gray-01);
}

.headerItem2 {
    grid-row: 2;
    grid-column: 1/-1;
    justify-content: unset;
    margin-bottom: 0;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    gap: 1rem;
    /* justify-content: space-between; */
}

.headerItem3 {
    grid-row: 3;
    grid-column: 1/9;

    display: flex;
    gap: 1rem;
    /* padding: 1rem 2rem;
    align-self: end; */
}

.headerItem4 {
    grid-row: 3/4;
    grid-column: 8/-1;
    align-self: baseline;
    /* background-color: aqua; */
}

.imageComponentWrapper {
    display: none;
}

.listActionButtonsWrapper {
    /* display: flex;
    gap: 1rem; */
    /* margin-top: 2rem; */
    margin-bottom: 2rem;
    /* background-color: var(--color-gray-05);
    background-size: 400% 400%;
    background-image: linear-gradient(100deg, red, black, var(--color-red-03));
    animation: gradientAnimation 10s ease infinite; */
    padding: 1rem 0;
  
}
.listActionButtons{
    display: flex;
}
/* .listActionButtonsWrapper button {
    width: max-content;
} */

@media (min-width: 430px) {
    .imageComponentWrapper {
        height: 11rem;
        width: auto;
        display: flex;
    }

    .imageComponentWrapper img {
        height: 100%;
        width: 100%;
        object-fit: contain;
    }
}
@keyframes gradientAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}
